.App {
	text-align: center;
}

.App-header {
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.hidden {
	opacity: 0;
	-webkit-transition: opacity 2s;
	-moz-transition: opacity 2s;
	-o-transition: opacity 2s;
	transition: opacity 2s;
}

#header-text {
	position: absolute;
	margin-top: auto;
	-moz-transition: all 0.8s;
	-webkit-transition: all 0.8s;
	-o-transition: all 0.8s;
	transition: all 0.8s;
}

.intro-wrapper {
	background-color: #181a1a;
	border: 2px solid #111;
	border-radius: 5px;
	width: 95%;
	height: 90%;
	margin: auto;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 2em;
	grid-template-areas:
		'nav-wrapper'
		'content-wrapper';
}
.nav-wrapper {
	grid-area: nav-wrapper;
	border-bottom: 2px solid #111;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.content-wrapper {
	padding: 10px;
	overflow-y: scroll;
}
div.content-wrapper::-webkit-scrollbar {
	width: 5px;
}
div.content-wrapper::-webkit-scrollbar-track {
	border-radius: 10px;
}
div.content-wrapper::-webkit-scrollbar-thumb {
	background-color: #333;
	border-radius: 10px;
}
.main-content {
	height: 100%;
	display: grid;
	grid-template-columns: 1fr 1.5fr;
	grid-template-areas: 'left-column right-column';
	margin: 15px 0px;
}
.left-column {
	width: 90%;
	grid-area: left-column;
	padding: 20px;
}
.right-column {
	width: 90%;
	grid-area: right-column;
	padding: 20px;
}

.container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
}

.buttons-container {
	margin: 20px 10px;
}

.dots-wrapper {
	display: flex;
	padding: 10px;
}

.dot {
	height: 15px;
	width: 15px;
	border-radius: 50%;
	margin: 5px;
	-webkit-box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
	box-shadow: -1px 1px 3px -1px rgba(0, 0, 0, 0.75);
}

.contact-nav {
	padding: 10px;
}

.right-column:svg {
	border: 1px solid #fff;
}

svg {
	padding: 5px 8px;
}

.btn {
	padding-top: 10px;
	padding-bottom: 10px;
	color: #fff;
	background-color: #273443;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	transition: 0.5s;
	width: 100%;
}
.btn:hover {
	background-color: #364f6d;
}
.back-btn {
	width: 10%;
}

.progress-block {
	width: 100%;
	display: flex;
	align-items: center;
	min-height: 5vh;
}

.progress-text-container {
	min-width: 25%;
	max-width: 25%;
	margin: 5px 10px 5px 0;
}
.progress-text {
	align-text: center;
	height: 33px;
	grid-template-rows: 1fr 1fr;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: center;
}
.progress-text .large {
	font-size: 0.8em;
}
.progress-text .small {
	font-size: 0.5em;
}
.progress {
	height: 10px;
	width: 90%;
	position: relative;
	background: #444;
	border-radius: 25px;
	padding: 3px;
	margin-right: 0;
	margin-left: auto;
}
.progress > span {
	display: block;
	height: 100%;
	border-radius: 20px;
	position: relative;
	overflow: hidden;
	animation: animate 3s ease;
}
@keyframes animate {
	0% {
		width: 0;
		box-shadow: 0 0 20px red;
		background-color: #fff;
	}
	35% {
		box-shadow: 0 0 20px red;
		background-color: #fff;
	}
	50% {
		box-shadow: 0 0 20px red;
		background-color: #fff;
	}
	75% {
		box-shadow: 0 0 20px red;
		background-color: #fff;
	}
	85% {
		box-shadow: 0 0 20px green;
	}
	100% {
		box-shadow: 0 0 30px green;
	}
}
.svg-link {
	fill: #fff;
	transition: fill 0.5s, opacity 2s;
}
.svg-link:hover {
	fill: #0fa;
}

@media screen and (max-width: 950px) {
	.progress-text .large {
		font-size: 0.7em;
	}
	.progress-text .small {
		font-size: 0.4em;
	}
	.back-btn {
		width: 50%;
	}
	.main-content {
		grid-template-columns: 1fr;
		grid-template-areas:
			'left-column'
			'right-column';
	}
	.left-column {
		margin: auto;
		padding: 0px;
	}
	.right-column {
		margin: auto;
		padding: 0px;
	}
	.right-column svg {
		margin: 10px;
	}
	.progress {
		margin: 10px 0;
		width: 90%;
		margin: auto;
	}
	.progress-block {
		margin: 5px 0px;
	}
	.progress-text {
		width: 100%;
		margin: 0;
		text-align: left;
		display: block;
		font-size: 1.5rem;
	}
}

.card {
	background-color: #1d2329;
	border-radius: 4px;
	box-shadow: 0 0 2px #000;
	padding: 15px;
	margin: 20px;
	gap: 20px;
	display: flex;
	flex-direction: row;
	align-items: center;
	transition: 0.5s;
}

.card-text div h2 {
	font-size: 2rem;
}

.card-text p {
	font-size: 1.2rem;
}

@media screen and (max-width: 950px) {
	.card {
		gap: 7px;
		margin: 10px 0;
	}
	.card-text div h2 {
		font-size: 1.2rem;
	}
	.card-text p {
		font-size: 0.9rem;
	}
}

.card-image {
	width: 30%;
	border-radius: 4px;
	border: 2px solid #fff;
	cursor: pointer;
	transition: 0.2s;
}

.card-image:hover {
	transform: scale(1.02);
	box-shadow: 0 0 2px #000;
}

.card-text {
	text-align: left;
}

.modal-backdrop {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: center;
}

.modal {
	position: absolute;
	background-color: white;
	padding: 5px;
	background-color: #222;
	border-radius: 4px;
	margin: 0 10px;
}

.modal img {
	display: flex;
	width: 100%;
	height: auto;
}

.modal button {
	margin-top: 10px;
}

.card-badge {
	display: flex;
	gap: 7px;
	color: #000;
	border-radius: 4px;
	flex-wrap: wrap;
}

.badge {
	border-radius: 20px;
	padding: 3px 10px;
	font-size: 1rem;
	transition: 0.2s;
}

@media screen and (max-width: 950px) {
	.card-badge {
		justify-content: center;
	}
	.badge {
		font-size: 0.8rem;
	}
}

.badge:hover {
	box-shadow: 0 0 5px #000;
	transform: scale(1.05);
}

.card-buttons {
	display: flex;
	gap: 10px;
	align-items: center;
	justify-content: flex-end;
	flex: 1;
}

a.btn {
	color: #fff;
	font-size: 16px;
}

a.btn.code {
	background-color: #254d50;
	width: 100px;
}

a.btn.code:hover {
	background-color: #3e7d7f;
}

a.btn.demo {
	background-color: #4caf50;
	width: 100px;
}

a.btn.demo:hover {
	background-color: #2e7d32;
}

/* in smaller screen sizes, the image in card is on top then the content on the bottom */
@media screen and (max-width: 950px) {
	.card {
		flex-direction: column;
	}
	.card-image {
		width: 100%;
	}
	.card-text {
		width: 100%;
	}
	.card-badge {
		width: 100%;
	}
	.card-buttons {
		width: 100%;
	}
}

.badges-and-buttons-container {
	gap: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

/* in even smaller screen sized, card-buttons and card-badge are stacked on top of each other */
@media screen and (max-width: 600px) {
	.badges-and-buttons-container {
		flex-direction: column;
		gap: 10px;
	}

	.card-buttons {
		width: 100%;
		justify-content: center;
	}
}

.icon {
	transition: 2s;
}

.icon:hover {
	transform: scale(1.1);
	filter: drop-shadow(0px 0px 3px #000);
}
